.MiniModal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.MiniModal__container {
  position: relative;
  top: 0rem;
  background-color: #ffffff;
  padding: 1rem;
  width: 280px;
  border-radius: 23px;
  background-color: var(--bg-color);
  color: #ff9f22;
  box-shadow: 0 4px 8px;
}

.MiniModal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: #f5f5f5;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  border-bottom-right-radius: initial;
  border-top-left-radius: initial;
  background: #ff9f22;
  color: black;
  font-weight: 700;
}
