.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.93);
  overflow: scroll;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Modal__container {
  position: relative;
  background-color: var(--bg-color);
  color: #ff9f22;
  box-shadow: 0 4px 8px;
  top: 0rem;
  padding: 1rem;
  width: 400px;
  border-radius: 15px;
  margin-bottom: 60px;
  margin-top: 50px;
}

.Modal__close-button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  background-color: #f5f5f5;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  border-bottom-right-radius: initial;
  border-top-left-radius: initial;
  background: #ff9f22;
  color: black;
  font-weight: 700;
}
@media screen and (max-height: 389px) {
  .Modal__container {
    height: max-content;
    top: 5rem;
  }
  .join-modal p {
    font-size: 20px;
  }
}
@media screen and (max-width: 630px) {
  .Modal__container {
    width: 90%;
  }
}
